export default function Footer() {
    return (
      <footer className="bg-blue-700 text-white py-4 px-6 flex items-center justify-between">
        {/* Texto izquierda */}
        <p className="text-xs md:text-sm font-pixel">
          Gracias por su confianza!
        </p>
  
        {/* Contáctenos + íconos */}
        <div className="flex items-center space-x-3 text-xs md:text-sm font-pixel">
          <span>Contáctenos!</span>
          <a href="https://twitter.com" target="_blank" rel="noopener noreferrer">
            <i className="fab fa-twitter hover:text-gray-300 transition" />
          </a>
          <a href="https://facebook.com" target="_blank" rel="noopener noreferrer">
            <i className="fab fa-facebook hover:text-gray-300 transition" />
          </a>
          <a href="https://instagram.com" target="_blank" rel="noopener noreferrer">
            <i className="fab fa-instagram hover:text-gray-300 transition" />
          </a>
          <a href="https://tiktok.com" target="_blank" rel="noopener noreferrer">
            <i className="fab fa-tiktok hover:text-gray-300 transition" />
          </a>
          <a href="https://youtube.com" target="_blank" rel="noopener noreferrer">
            <i className="fab fa-youtube hover:text-gray-300 transition" />
          </a>
        </div>
      </footer>
    );
  }